import React from 'react'


const Info = ({ peoples, setPeoples }) => {
    const deleteItem = (id) => {
        const newPeople = peoples.filter((akash) => akash.id !== id)
        setPeoples(newPeople)
    }
    return (
        <>
            <h1>{peoples.length} Birthdays Today</h1>
            <hr />
            {
                peoples.map((person) => {
                    const { id, img, name, age } = person;
                    return (
                        <div className='container' key={id}>
                            <div className="row">
                                <div className="col-sm-3">
                                    <img src={img} alt="" />
                                </div>
                                <div className="col-sm-6">
                                    <h3 className='lead'>{name}</h3>
                                    <h6 className='lead'>Age:{age}</h6>
                                </div>
                                <div className="col-sm-3">
                                    <button
                                        type="submit"
                                        className='btn btn-danger'
                                        onClick={() => { deleteItem(id) }} >
                                        Remove</button>
                                </div>
                            </div>

                        </div>
                    )
                })
            }
        </>
    )
}

export default Info
