import React from 'react'
import Info from './Card-info'
import { data } from './data'

const Card = () => {
    const [peoples, setPeoples] = React.useState(data)
    const deleteAll = () => {
        setPeoples([])
    }
    return (
        <div>
            <div className="card">
                <Info peoples={peoples} setPeoples={setPeoples} />
                <button type="submit" className='btn' onClick={deleteAll}>Delete All</button>
            </div>
        </div>
    )
}

export default Card
